nav {
  background-color: #171717;
  height: 5%;
}

a {
  color: white;
  text-decoration: none;
  font-size: 105%;
}

.navbar {
  display: grid;
  grid-template-columns: 75% 25%;
}

.top-image {
  width: 100%;
  height: auto;  
}

.nav-logo{
  width: 50%;
}

.logo {
  width: 10%;
  height: auto;
  margin-left: 2vw;
  margin-top: 1vw;
  position: absolute;
  z-index: 1;
}

.choices-list {
  list-style: none;
  padding-right: 2%;
  padding-left: 0;
  margin-left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /* grid-template-columns: repeat(4, 6em); */
  grid-gap: 1%;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  justify-items: end;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.background-scratches {
  width: 100%;
  height: 100%;
  opacity: 0.6;
  position: fixed;
  z-index: -1;
}



